*{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-color: black;
  font-size: large;
  background-image: url(../src/assets/puert.jpg);
  
}

body {
  padding: 60px;
  text-align: center;
}

input {
  color: black;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid{

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}


.card{

  align-content: center;
  border: 1px solid black;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;

}

.card p {

  padding: 5px;
  text-align: center;

}

.card img {
  max-height: 170px;
}